/* ImageWithText.css */

.image-container {
  position: relative;
  width: 100%;
  max-height: 400px; /* Set the maximum height of the container */
  overflow: hidden;
}

.background-image {
  width: 100%;
  height: auto;
  display: block;
}

.lgx-banner-info {
  position: absolute;
  top: 60%; /* Adjust this value to vertically center the text */
  left: 30%; /* Adjust this value to horizontally center the text */
  transform: translate(-50%, -50%);
  text-align: center;
  color: #fff; /* Set the text color */
}

.text-overlay h1 {
  font-size: 2.5em; /* Set the font size for the heading */
  margin-bottom: 10px;
}

.text-overlay p {
  font-size: 1.2em; /* Set the font size for the paragraph */
}

/* Breadcrumb Styles */
