/* Event Crad */

.lgx-inner {
  padding: 50px 0;
}

.lgx-heading {
  text-align: center;
  margin-bottom: 30px;

  h2 {
    font-size: 2.5rem;
    color: #333;
    margin-bottom: 10px;
  }

  i {
    color: #f00;
  }
}

.lgx-event {
  border: 1px solid #ddd;
  transition: transform 0.3s ease-in-out;

  &:hover {
    transform: translateY(-10px);
  }

  a {
    text-decoration: none;
    color: inherit;
  }

  .lgx-event__tag {
    padding: 8px;
    background-color: #f00;
    color: #fff;
    text-align: center;
    font-weight: bold;
  }

  .lgx-event__image {
    overflow: hidden;
    img {
      width: 100%;
      height: auto;
    }
  }

  .lgx-event__info {
    padding: 20px;

    .meta-wrapper {
      display: flex;
      justify-content: space-between;
      margin-bottom: 10px;
      color: #555;
      font-size: 0.9rem;
    }

    .title {
      font-size: 1.2rem;
      margin-bottom: 5px;
    }

    .sub-title {
      font-size: 1rem;
      height: 20px;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    .text-primary {
      color: #f00;
    }
  }

  .lgx-event__footer {
    padding: 10px;

    div {
      margin-bottom: 5px;
    }
  }

  .lgx-event__category {
    span {
      display: none;
    }
  }
}

.section-btn-area {
  text-align: center;
  margin-top: 30px;

  a {
    text-decoration: none;
    color: #fff;
  }

  .lgx-btn {
    background-color: #f00;
    padding: 10px 20px;
    font-size: 1rem;
    display: inline-block;
    border-radius: 5px;
    transition: background-color 0.3s ease-in-out;

    i {
      margin-right: 5px;
    }

    &:hover {
      background-color: #333;
    }
  }
}

.lgx-heading {
  display: block;
  margin-bottom: 4rem;
